import React from 'react'

export interface BannerProps {
    class?: string
    titleImage: string
    header: JSX.Element
    buttonTitle?: string
}

const Banner: React.FC<BannerProps> = (props) => {
    const button = props.buttonTitle ? ( 
      <ul className="actions">
        <li><a href="#" className="button big">{props.buttonTitle}</a></li>
      </ul>) : null

    return (
        <section id={"banner"} className={props.class}>
        <div className="content">
          <header>
              {props.header}
          </header>
          <p>{props.children}</p>
          {button}
        </div>
        <span className="image object">
          <img src={props.titleImage} alt="" />
        </span>
      </section>
    )
}

export default Banner