import React from 'react';
import './assets/sass/main.scss';
import './assets/css/praxis.css';
import utilJS from './assets/js/util';
import mainJS from './assets/js/main';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Startpage from './pages/Startpage';
import Ergotherapie from './pages/Ergotherapie';
import Praxis from './pages/Praxis';
import Team from './pages/Team';
import ErgotherapieKinder from './pages/ErgotherapieKinder';
import Konzentrationstraining from './pages/Konzentrationstraining';
import SozialesKompetenztraining from './pages/SozialesKompetenztraining';
import ErgotherapieErwachsene from './pages/ErgotherapieErwachsene';



export enum Routes {
  start = "/",
  ergotherapie = "/ergotherapie/",
  ergotherapieKinder = "/ergotherapie-kinder/",
  sozialesKompetenztraining = "/soziales-kompetenztraining/",
  konzentrationstraining = "/konzentrationstraining/",
  praxis = "/praxis/",
  team = "/team/",
  ergotherapieErwachsene = "/ergotherapie-kinder/"
}

class App extends React.Component {

  componentDidMount() {
    utilJS()
    mainJS()

  }

  render() {
    return (
      <Router>
        <Switch>
          <Route path={Routes.ergotherapie} component={Ergotherapie}/>  
          <Route path={Routes.ergotherapieKinder} component={ErgotherapieKinder}/> 
          <Route path={Routes.sozialesKompetenztraining} component={SozialesKompetenztraining}/>  
          <Route path={Routes.konzentrationstraining} component={Konzentrationstraining}/>  
          <Route path={Routes.praxis} component={Praxis}/>  
          <Route path={Routes.team} component={Team}/>  
          <Route path={Routes.ergotherapieErwachsene} component={ErgotherapieErwachsene}/>  
          <Route component={Startpage} />
        </Switch>
      </Router>
    );
  }
}

export default App;
