import React from 'react'

export interface ArticleTeaserProps {
    id?: string
    titleImage: string
    title: string
    buttonTitle?: string
}

const ArticleTeaser: React.FC<ArticleTeaserProps> = (props) => {

    let button = props.buttonTitle ? (
        <ul className="actions">
                <li><a href="#" className="button">{props.buttonTitle}</a></li>
        </ul>
    ) : null

    return (
        <article id={props.id}>
            <a href="#" className="image"><img src={props.titleImage} alt="" /></a>
            <h3>{props.title}</h3>
            <p>{props.children}</p>
            {button}
        </article>
    )
}

export default ArticleTeaser